// Vars

:root {
  // base
  --base-txt-color: #222;
  --heading-color: #F2F2F2;
  --base-font-family: "aktiv-grotesk", sans-serif;
  --body-bg: #F2F2F2;
  

  // colors  
  $brand-color: #222633;
  --brand-color: #{$brand-color};
  --link-color: --brand-color;
  
  // app header
  --app-header-height: 80px;
  --app-header-color: #222633;
  --app-header-txt-color: #F2F2F2;
  --app-header-title-font-size: 1.6em;



  // shoelace tokens
  // full list here:
  // https://github.com/shoelace-style/shoelace/blob/current/src/styles/base.scss
  --sl-color-primary-50: #EDEBEB;
  --sl-color-primary-100: #{lighten($brand-color, 40%)};
  --sl-color-primary-200: #{lighten($brand-color, 30%)};
  --sl-color-primary-300: #{lighten($brand-color, 20%)};
  --sl-color-primary-400: #{lighten($brand-color, 10%)};
  --sl-color-primary-500: #{$brand-color};
  --sl-color-primary-600: #{darken($brand-color, 10%)};
  --sl-color-primary-700: #{darken($brand-color, 20%)};
  --sl-color-primary-800: #{darken($brand-color, 30%)};
  --sl-color-primary-900: #{darken($brand-color, 40%)};
  --sl-focus-ring-color-primary: #{rgba($brand-color, 0.1)};

  // Secondary colour
  --sl-color-secondary-50: #222633;
  --sl-color-secondary-100: #{lighten($brand-color, 40%)};
  --sl-color-secondary-200: #{lighten($brand-color, 30%)};
  --sl-color-secondary-300: #{lighten($brand-color, 20%)};
  --sl-color-secondary-400: #{lighten($brand-color, 10%)};
  --sl-color-secondary-500: #{$brand-color};
  --sl-color-secondary-600: #{darken($brand-color, 10%)};
  --sl-color-secondary-700: #{darken($brand-color, 20%)};
  --sl-color-secondary-800: #{darken($brand-color, 30%)};
  --sl-color-secondary-900: #{darken($brand-color, 40%)};
}